* {
  margin: 0;
  padding: 0;
}

.overlay {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3;
}

.modal-cont {
  width: 400px;
  min-height: 150px;
  background-color: #fff;
  position: relative;
  border-radius: 5px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  padding: 10px;
  font-family: "Inter", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header-modal {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5px;
  padding-bottom: 5px;
  color: #228522;
}

.recordList {
  width: 85%;
  padding: 5px;
  margin-bottom: 25px;
  line-height: 20px;
}

.newGame-button {
  min-width: 80px;
  height: 30px;
  padding: 5px;
  margin-bottom: 10px;
  background-color: #5035ff;
  border-radius: 5px;
  color: #fff;
  border: none;
  cursor: pointer;
}
